"use client";

import { useState } from "react";

export default function CareerPage() {
  const [form, setForm] = useState({
    fullName: "",
    email: "",
    phone: "",
    address: "",
    pin: "",
    message: "",
  });
  const [cv, setCv] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleChange = (e: any) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setSuccess(null);

    if (!form.fullName || !form.email || !form.phone) {
      setError("Please fill all required fields.");
      return;
    }

    setLoading(true);
    try {
      const fd = new FormData();
      Object.entries(form).forEach(([key, value]) => fd.append(key, value));
      if (cv) fd.append("cv", cv);

      const res = await fetch("/api/career", {
        method: "POST",
        body: fd,
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data?.error || "Failed to submit");

      setSuccess("Application submitted â€” thank you! We will contact you soon.");
      setForm({ fullName: "", email: "", phone: "", address: "", pin: "", message: "" });
      setCv(null);
    } catch (err: any) {
      setError(err.message || "Submission failed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <section className="bg-gray-50 py-12">

      {/* ðŸ”¹ Page Header Section */}
      <div className="text-center mb-12 px-5">
        <h1 className="text-4xl font-bold" style={{ color: "#0A1B6F" }}>Join Our Team</h1>
        <p className="text-gray-600 mt-3 text-lg max-w-2xl mx-auto">
          Build your career with us. Fill the form below and our HR team will contact you soon.
          We are looking for talented, dedicated and passionate people!
        </p>
      </div>

      {/* ðŸ”¹ Form Card */}
      <div className="max-w-3xl mx-auto px-5">
        <form
          onSubmit={onSubmit}
          className="space-y-4 p-6 border rounded-lg bg-white shadow-md"
        >
          <h4 className="text-xl font-semibold mb-4" style={{ color: "#0A1B6F" }}>Apply Now</h4>

          {/* Full Name */}
          <div>
            <label className="block text-sm mb-1 font-medium">Full Name*</label>
            <input
              name="fullName"
              value={form.fullName}
              onChange={handleChange}
              required
              className="w-full border rounded px-3 py-2"
              placeholder="Enter your name"
            />
          </div>

          {/* Email */}
          <div>
            <label className="block text-sm mb-1 font-medium">Email*</label>
            <input
              type="email"
              name="email"
              value={form.email}
              onChange={handleChange}
              required
              className="w-full border rounded px-3 py-2"
              placeholder="Enter your email"
            />
          </div>

          {/* Phone */}
          <div>
            <label className="block text-sm mb-1 font-medium">Phone Number*</label>
            <input
              name="phone"
              value={form.phone}
              onChange={handleChange}
              required
              className="w-full border rounded px-3 py-2"
              placeholder="Enter phone number"
            />
          </div>

          {/* Address */}
          <div>
            <label className="block text-sm mb-1 font-medium">Address*</label>
            <input
              name="address"
              value={form.address}
              onChange={handleChange}
              required
              className="w-full border rounded px-3 py-2"
              placeholder="Enter your address"
            />
          </div>

          {/* Pin Code */}
          <div>
            <label className="block text-sm mb-1 font-medium">Pin Code*</label>
            <input
              name="pin"
              value={form.pin}
              onChange={handleChange}
              required
              className="w-full border rounded px-3 py-2"
              placeholder="Enter pin code"
            />
          </div>

          {/* Message */}
          <div>
            <label className="block text-sm mb-1 font-medium">Message</label>
            <textarea
              name="message"
              value={form.message}
              onChange={handleChange}
              rows={4}
              className="w-full border rounded px-3 py-2"
              placeholder="Tell us something about you..."
            />
          </div>

          {/* File Upload */}
          <div>
            <label className="block text-sm mb-1 font-medium">Upload CV/Resume</label>
            <input
              type="file"
              accept=".pdf,.doc,.docx"
              onChange={(e) => setCv(e.target.files?.[0] ?? null)}
            />
            {cv && <div className="text-sm mt-1 text-gray-600">{cv.name}</div>}
          </div>

          {/* Submit Button */}
          <div>
            <button
              type="submit"
              disabled={loading}
              className="px-5 py-2 text-white rounded transition font-medium"
              style={{ backgroundColor: "#0A1B6F" }}
            >
              {loading ? "Submitting..." : "Submit Application"}
            </button>
          </div>

          {/* Success & Error */}
          {success && <div className="text-green-600 mt-2 font-medium">{success}</div>}
          {error && <div className="text-red-600 mt-2 font-medium">{error}</div>}
        </form>
      </div>

    </section>
  );
}
